.container{
    text-align: center;
}
.title{
    color: white;
    margin-top: 60px;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-shadow: 2px 2px 5px #0f1b21;
}
.title span{
    color: rgb(0, 162, 255);
    margin-left:.5em;
    text-shadow: 2px 2px 5px #0f1b21;
}
.reset{
    padding: 10px 45px;
    background: transparent;
    border: 2px solid aqua;
    color: white;
    font-size: 15px;
    font-weight: 800;
    border-radius: 8px ;
    box-shadow: 2px 1px 5px rgb(176, 173, 173);
    cursor: pointer;
}
.reset:hover{
background: #83848586;
border:2px solid rgba(0, 131, 212, 0.632) ;
box-shadow: 2px 2px 5px #0f1b21;
transition: 0.3s;
}
.boxes{
    display: flex;
    height: 14vh;
    width: 9vw;
    background:linear-gradient(to right, rgba(53, 52, 52, 0.205) , rgba(136, 136, 136, 0.447));
    box-shadow: 2px 2px 5px #0f1b21;
    border-radius: 12px;
    cursor: pointer;
    margin: 10px 10px 0 0;
}
.boxes img{
    margin: 10px 25px;
}
.board{
    height: 50vh;
    width: 28vw;
    display: flex;
    margin: auto;
}

.title img{
    padding: 20px 20px;
    height: 50px;
}
@media(Max-width : 820px){
    .title{
        font-size:1.9em;
    }
    .title img{
        padding: 10px 10px;
    }
    .board{
        width: 95vw;
    }
    .boxes{
        width: 30vw;
        height:13vh
    }
    .boxes img{
        margin: 10px 20px;
    }
}